<template>
  <div class="order-notification-list">
    <van-nav-bar title="订单通知" left-text="" left-arrow @click-left="$router.back()" />
    <order-notification-list-card
      :updatetime="data.updatetime"
      :suanli="data.suanli"
      :status="data.status"
      :order_id="data.order_id"
      :order_no="data.order_no"
      :money="data.money"
      :currencytype="data.currencytype"
      :chuangjiantime="data.chuangjiantime"
      v-for="data in orderDetailData"
      :key="data.id"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent, onMounted, ref } from "vue"
import { NavBar, Toast } from "vant"
import OrderNotificationListCard from "./OrderNotificationListCard.vue"
import OrderService, { orderDetail } from "@/services/order"

export default defineComponent({
  name: "OrderNotificationList",
  components: {
    OrderNotificationListCard,
    [NavBar.name]: NavBar,
    [Toast.name]: Toast,
  },
  setup() {
    let orderDetailData = ref<orderDetail[]>([])

    function getDetailOrders() {
      OrderService.getDetailOrders().then(({ status, data, message }) => {
        if (status !== 200) {
          Toast(message)
          return
        }
        orderDetailData.value = data
      })
    }

    onMounted(getDetailOrders)

    return { orderDetailData }
  },
})
</script>
